.main-wrapper {
  height: 80vh;
  width: 300px;
  position: absolute;
  top: 50px;
  left: 50px;
  z-index: 1000;
}

.clock-wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  border-radius: 30px;
  margin-top: 20px;
  border: 1px solid #334066;
  background-color: rgba(black, 0.1);
  padding: 10px;
  backdrop-filter: blur(10px);
}
.clock-title {
  color: white;
  font-family: 'SF Pro Display', sans-serif;
  font-weight: bold;
  margin: 0 0 0 30px;
  font-size: 30px;
  line-height: 1;
}
.city-input {
  border: 1px solid #334066;
  background-color: rgba(black, 0.1);
  padding: 10px;
  backdrop-filter: blur(10px);
  color: white;
  border-radius: 30px 0 0 30px;

  &:focus {
    outline: none;
  }
}

.city-submit {
  border: 1px solid #334066;
  background-color: rgba(black, 0.1);
  padding: 10px;
  backdrop-filter: blur(10px);
  color: white;
  border-radius: 0 30px 30px 0;
}
